export default "/**\n * This file is going to be used by customization components\n * for monaca editor.\n */\n\nexport interface SpanLog {\n  /**\n   * Log timestamp, in us\n   */\n  timestamp: number;\n  /**\n   * Log `fields` object\n   */\n  fields: { [key: string]: string };\n}\n\nexport interface Span {\n  /**\n   * Span ID\n   */\n  id: string;\n  /**\n   * Span's Trace ID\n   */\n  traceId: string;\n  /**\n   * Operation name\n   */\n  operationName: string;\n  /**\n   * Span start timestamp, in us\n   */\n  startTime: number;\n  /**\n   * Span finish timestamp, in us\n   */\n  finishTime: number; // us\n  /**\n   * Span reference objects\n   */\n  references: {\n    type: 'childOf' | 'followsFrom';\n    spanId: string;\n    traceId: string;\n  }[];\n  /**\n   * Span tags object\n   */\n  tags: { [key: string]: string };\n  /**\n   * Span logs array\n   */\n  logs: SpanLog[];\n  /**\n   * Jaeger-specific `process` object\n   */\n  process?: {\n    /**\n     * Service name\n     */\n    serviceName: string;\n    /**\n     * Jaeger gives id like: `p1`, `p2`...\n     */\n    id: string; // `p1`, `p2` like\n    /**\n     * Process tags object\n     */\n    tags: { [key: string]: string };\n  };\n  /**\n   * Zipkin-specific `localEndpoint` object\n   */\n  localEndpoint?: {\n    /**\n     * Service name\n     */\n    serviceName: string;\n    ipv4?: string;\n    port?: number;\n  };\n}\n\nexport interface Trace {\n  /**\n   * Spans array\n   */\n  spans: Span[];\n  /**\n   * Timestamp of the earliest span in us\n   */\n  startTime: number;\n  /**\n   * Finish timestamp of the latest span in us\n   */\n  finishTime: number;\n  /**\n   * Duration of a trace in us (finishTime - startTime)\n   */\n  duration: number;\n  /**\n   * Root span\n   */\n  rootSpan: Span | undefined;\n  /**\n   * Trace ID\n   */\n  id: string;\n  /**\n   * Root span's operation name (if exists)\n   */\n  name: string | undefined;\n  /**\n   * Total span count\n   */\n  spanCount: number;\n  /**\n   * Count of spans that have `error` tag\n   */\n  errorCount: number;\n}\n";